$main-color: #232323;
body{
    background-color: $main-color;
    color: #fff;
    font-family: Fira Code;
    overflow: hidden;
}
header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo .logo__svg{
    width: 90%;
    margin-left: 15px;
}
.logo-git{
    width: 21%;
    display: flex;
    justify-content: space-between;
}
.github-logo{
    width: 65px;
    margin-left: 20px;
    margin-top: 0px;
}
 .nav-list{
    max-width: 100%;
    list-style: none;
    display: flex;
    margin-right: 10px;
    height: 80px;
    transform: translateY(-65px);
     &__element{
        border: 3px solid #FFFFFF;
        box-sizing: border-box;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        padding: 20px;
        font-size: 18px;
        line-height: 46px;
        text-align: center;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        cursor: pointer;
    }
    &__link{
        text-decoration: none;
        color: #fff;
    }
    &__element:hover{
        background-color: #ffffff;
        color: $main-color;
        .nav-list__link{
            color: $main-color;
        }

    }
    &__element:nth-child(3){
        border-left: none;
    }
}
.content{
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);
}
.app{
    border: 7px solid #fff;
    height: 720px;
    width: 480px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    &__title{
        font-family: Fira Code;
        font-style: normal;
        font-weight: normal;
        font-size: 64px;
        line-height: 16px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
    }
    &__text{
        transform: translateY(-60px);
    }
    &__button{
        border: none;
        width: 262px;
        height: 61px;
        background: #FFFFFF;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-style: normal;
        font-family: Fira Code;
        font-weight: 600;
        font-size: 18px;
        line-height: 16px;
        letter-spacing: 7.5px;
        text-transform: uppercase;
        color: $main-color;
    }
    &__button:hover{
        background-color: #c9c9c9;
        cursor: pointer;
    }
   &__view-code:hover{
       cursor: pointer;
   }
}
.app:nth-child(odd){
    height: 613px;
}
.app:nth-child(1){
    border-right: none;
}
.app:nth-child(3){
    border-left: none;
}
.title-info{
    font-size: 64px;
    transform: translateY(-100px);
    letter-spacing: 0.5px;
    text-transform: uppercase;
    font-style: normal;
    font-weight: normal;
    opacity: 0;
}
.container-info{
    border: #fff 7px solid;
    position: absolute;
    top: 50%;  
    left: 50%; 
    transform: translate(-50%, -5%);
    text-align: center;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 500px;
    width: 1200px;
    &__logo{
        width: 30%;
        transform: translateY(-30px);
    }
    &__text{
        font-size: 24px;
        line-height: 44px;
        text-align: center;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
}